
<template>
 <el-main>
    <el-form ref="form" :model="form" label-width="80px">
         <p>{{$route.query.supplier_id}}</p>
        <el-form-item label="供应商名称">
            <el-input v-model="form.supplier_name" ></el-input>
        </el-form-item>
        <el-form-item label="排序">
            <el-input v-model="form.supplier_sort" ></el-input>
        </el-form-item>
        
        <el-form-item>
            <el-button type="primary" @click="onSubmit">立即创建</el-button>
            <el-button>取消</el-button>
        </el-form-item>
    </el-form>
     </el-main>
</template>
<script>
  export default {
    data() {
      return {
         form: {
          supplier_name:'',
          supplier_sort:'',
        }
        
      };

    },
    mounted:function(){
      if(this.$route.query.supplier_id!=0)
      {
          console.log(11);
        this.editgoods();
      }else{
         console.log(22);
      }
      
    },
    
    methods: {
        //编辑商品
      editgoods(){
          var _this=this;
          this.$axios({
              method:'get',
              url:this.url()+'/index.php/supplier/onesupplier',
              params:{
                  supplier_id:_this.$route.query.supplier_id
              }
          }).then(function(res){
              _this.form=res.data.data
              //console.log(res.data.data);
              //alert("111");
          });
       }, 
      //提交表单
      onSubmit() {
          var _this = this;
          console.log(_this.form);
          this.$axios({
              method: 'post',
              url:this.url()+'/index.php/supplier/addsupplier',
              params : {
                data:_this.form,
                supplier_id:_this.$route.query.supplier_id
              }
          }).then(function(res){
                alert(res.data.data);
                // location.href = "/right";
                _this.$router.push('/supplier');
                console.log(res.data);
          });
        //console.log(this.form);
      }
    }
  };
</script>






